import Boop from "./Sounds/Time is up.mp3";
import Beep from "./Sounds/Beep.mp3";
import Ping from "./Sounds/Ping.mp3";
import Drums from "./Sounds/Drums.mp3";
import Violin from "./Sounds/Violin.mp3";
import DDD from "./Sounds/DDD_Mario.mp3";
import DKC from "./Sounds/DKC_FF.mp3";
import KidIcarus from "./Sounds/KIU.mp3";
import NFM from "./Sounds/MaximumOverfly.mp3";
import { useRef, useEffect } from "react";

function Keys({ setDisplay, volly }) {
  useEffect(() => {
    window.addEventListener("keydown", handleKey);
  }, []);

  const audino = useRef("");
  const handleKey = (event) => {
    const { key } = event;
    let reggie = new RegExp(/^[q,w,e,a,s,d,z,x,c]$/, "i");
    console.log(key);
    if (reggie.test(key)) {
      console.log(key);
      audioPlay(key.toUpperCase());
    } else {
      return null;
    }
  };

  function audioPlay(id) {
    if (audino.current !== "") {
      audino.current.pause();
      audino.current.currentTime = 0;
    }
    setDisplay(document.getElementById(id).innerHTML);
    audino.current = document.getElementById(id);
    audino.current.volume = volly / 100;
    audino.current.play();
  }
  return (
    <div className="KeyBox">
      <button
        id="Ping"
        className="drum-pad"
        onClick={() => audioPlay("Q")}
        onKeyPress={(event) => console.log(event.key)}>
        Q
        <audio id="Q" className="clip" src={Boop}>
          Time Running Out
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("W")}>
        W{" "}
        <audio id="W" className="clip" src={Beep}>
          Bleep
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("E")}>
        E
        <audio id="E" className="clip" src={Ping}>
          Ping
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("A")}>
        A
        <audio id="A" className="clip" src={Drums}>
          Drum Roll
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("S")}>
        S
        <audio id="S" className="clip" src={Violin}>
          Violin
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("D")}>
        D
        <audio id="D" className="clip" src={DDD}>
          Song Mario
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("Z")}>
        Z
        <audio id="Z" className="clip" src={DKC}>
          Song DKC
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("X")}>
        X
        <audio id="X" className="clip" src={KidIcarus}>
          Song KIU
        </audio>
      </button>
      <button id="Ping" className="drum-pad" onClick={() => audioPlay("C")}>
        C
        <audio id="C" className="clip" src={NFM}>
          Song NFM
        </audio>
      </button>
    </div>
  );
}
export default Keys;
