import Display from "./Display";
import Keys from "./Keys";
import Volume from "./Volume";
import { useState } from "react";

function DrumMachine() {
  const [displayer, setDisplayer] = useState("Press any button!");
  const [vol, setVol] = useState(50);
  return (
    <div id="drum-machine">
      <Display word={displayer} />
      <Keys setDisplay={setDisplayer} volly={vol} />
      <div>
        <Volume current={vol} newVol={setVol} />
      </div>
    </div>
  );
}

export default DrumMachine;
