function Volume({ current, newVol }) {
  function volChange(val) {
    if (current + val > 100) {
      return null;
    } else if (current + val < 0) {
      return null;
    } else {
      newVol(current + val);
    }
  }

  return (
    <div>
      Vol:
      <button onClick={() => volChange(-10)}>Decrease</button>
      {current}
      <button onClick={() => volChange(+10)}>Increase</button>
    </div>
  );
}

export default Volume;
